}

.example-card p {
  margin: 0;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  background-color: white;
  font-family: Nunito, sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.all {
    display: flex;
    flex-direction: column;
}

.contact {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    position: relative;
    z-index: 1;
    margin: 0 auto;
    max-width: 1400px;
}

/* SUBMIT MESSAGE SECTION CSS */

.submit-message {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-top: 5%;
}

.submit-message h2 {
    display: flex;
    justify-content: center;
    margin-bottom: 55px;

    font-family: 'Montserrat', sans-serif;
    font-size: 16px/24px;
    font-weight: bold;
    color: #262626;
    text-align: center;
    font-family: sans-serif;
    letter-spacing: 0px;
}

.box {
    align-self: flex-end;

    width: 445px;
    height: 456px;
    margin-top: 10px;
    box-sizing: border-box;
    box-shadow: 0px 3px 6px #00000029;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.box input,
.box select,
.box button {
    padding: 10px 0px;
    margin-bottom: 30px;
}

.box textarea {
    height: 110px;
    padding: 10px 0;
    margin-bottom: 40px;
    resize: none;
}

.test {
    width: 100%;
}

.box input,
.box select,
.box textarea {
    width: 340px;
    box-sizing: border-box;
    box-shadow: none;
    outline: none;
    border: none;
    border-bottom: 1px solid #707070;
}

.box button {
    margin-left: 33%;
    background: #F7022D 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 8px #CCCCCC;
    border-radius: 18px;
    border: none;
    opacity: 1;
    cursor: pointer;

    font-family: 'Montserrat', sans-serif;
    font-size: 16px/24px;
    font-weight: bold;
    color: #ffffff;
    text-align: center;
    font-family: sans-serif;
    letter-spacing: 0px;
    opacity: 1;

    width: 128px;
    height: 35px;

    transition: .5s;
}

select option {
    font-family: 'Calibri', sans-serif;
    font-weight: normal;
    font-size: 16px;
}

.box button:hover {
    box-shadow: 0px 5px 8px #B3B3B3;
}

.box form div {
    position: relative;
}


.box form div label {
    position: absolute;
    top: 10px;
    display: flex;
    justify-self: self-end;
    pointer-events: none;
    transition: .5s;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: normal;
    color: #262626;
    text-align: center;
    font-family: sans-serif;
    letter-spacing: 0px;
    opacity: 1;
}

.box input:focus ~ label,
.box select:focus ~ label,
.box textarea:focus ~ label,
.box input:valid ~ label,
.box select:valid ~ label,
.box textarea:valid ~ label {
    top: -12px;
    color: #F7022D;
    font-size: 12px;
    font-weight: bold;
}

.box input:focus,
.box select:focus,
.box textarea:focus,
.box input:valid,
.box select:valid,
.box textarea:valid {
    border-bottom: 1px solid #F7022D;
}


