@media screen and (max-width: 1024px) {
    #nav-toggle {
        position: absolute;
        z-index: 11;
        display: none;
    }

    .tipo-1 {
        display: none;
    }

    .tipo-2 {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 100%;
        height: 70px;
        position: fixed;
        padding: 0 5%;
        box-shadow: 0px 3px 6px #00000029;
        background-color: #FFFFFF;
        z-index: 10;
    }

    .logo-2 {
        float: left;
        align-self: flex-start;
        z-index: 10;
        transition: all .9s ease-in-out;
    }

    .tipo-2 .icon-burguer {
        position: absolute;
        right: 5%;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
    }

    .tipo-2 .icon-burguer .line {
        display: block;
        width: 30px;
        height: 5px;
        background-color: #9726E0;
        margin: 5px;
        border-radius: 3px;
        transition: all .3s ease-in-out;
    }

    .section {
        width: auto;
        height: 812px;
    }

    .links {
        display: flex;
        justify-content: space-between;
        position: absolute;
        float: none;
        align-items: center;
        z-index: 10;
        position: fixed;
        left: 0;
        right: 0;
        background: #ffffff;
        flex-direction: column;
        width: auto;
        height: 92%;
        margin-top: 70%;
        bottom: 120%;
        transition: all .5s ease-in-out;
    }

    .painting {
        background: url("/static/media/Ilustração - Navbar.609a9cdf.svg");
        background-repeat: no-repeat;
        align-self: flex-start;
        width: 285px;
        height: 305px;
    }

    .tipo-2 :checked ~ .links {
        bottom: 0;
    }

    .links .text-mobile,
    .links li a .button-mobile {
        list-style: none;
    }

    .tipo-2 :checked ~ .icon-burguer .line:nth-last-child(1) {
        transform: translateY(-10px) rotate(225deg);
    }

    .tipo-2 :checked ~ .icon-burguer .line:nth-last-child(3) {
        transform: translateY(10px) rotate(-225deg);
    }

    .tipo-2 :checked ~ .icon-burguer .line:nth-last-child(2) {
        opacity: 0;
    }

    .tipo-2 :checked ~ .logo {
        display: none;
    }

    .links .text-mobile {
        margin-top: 50px;
    }

    .links .text-mobile h3 {
        width: 170px;
        height: auto;
        text-align: center;

        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        color: #262626;

    }

    .links .text-mobile a:link {
        text-decoration: none;
    }

     .links .text-mobile .button-mobile {
        width: 160px;
        height: 35px;
        border: none;
        background-color: #F7022D;
        box-shadow: 0px 3px 8px #CCCCCC;
        border-radius: 18px;
        opacity: 1;
        font-family: 'Montserrat', sans-serif;
        font-weight: Bold;
        color: #FFFFFF;
        font-size: 18px;
    }

}

