* {
    box-sizing: border-box;
}

.all-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 810px;
    overflow-y: hidden;
}

.position-guests {
    margin-top: 100px;
}

.all-content h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 0;
    color: #262626;
    opacity: 1;

    text-align: left;
    margin-left: 10%;
}

.informs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 5%;
    align-items: center;
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    margin-top: 84px;
    z-index: 7;
}

.informs-reverse {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 10%;
}

.desc {
    width: 260px;
    height: 94px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.first {
    margin-left: 16%;
}

.desc-mentors {
    width: 260px;
    height: 94px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 16%;
}

.desc h6 {
    font-family: 'Montserrat', sans-serif;
    font-size: 21px;
    font-weight: bold;
    letter-spacing: 0;
    color: #262626;
    opacity: 1;

    text-align: left;;

    display: flex;
    justify-self: left;
}

.desc p {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0;
    color: #262626;
    opacity: 1;

    text-align: left;;

    display: flex;
    justify-self: left;
    width: 260px;
}

.test {
    width: 500px;
    height: 400px;
}

.staffs {
    margin-right: 12%;
}

.mentors {
    margin-left: 12%;
}

.hug-svg {
    background: url("/static/media/Ilustração - Convidados.ea1529dc.svg");
    background-repeat: no-repeat;
    position: absolute;
    width: 300px;
    height: 440px;
    top: 460px;
    z-index: 1;
    align-self: flex-end;
}

.aff {
    align-self: flex-end;
}

@media (max-width: 1170px) {
    .all-content {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .informs {
        flex-direction: column;
        justify-content: space-around;
    }
    
    .informs-reverse {
        flex-direction: column;
    }

    .desc,
    .desc-mentors,
    .staffs,
    .mentors {
        margin-right: 0%;
        margin-left: 0%;
        margin: 0 auto;
    }

    .all-content h2 {
        width: 200px;
        height: auto;
    }

    .hug-svg {
        display: none;
    }

    .desc h6 {
        font-size: 16px;
    }

    .informs {
        margin-top: 50px;
        height: 290px;
    }

    .informs-reverse {
        margin-top: 7%;
        height: 290px;
    }

    .logos {
        justify-content: flex-start;
    }

    .instagram, 
    .linkedin {
        margin-left: 9px;
    }

    .hug-svg-mobile {
        background: url(/static/media/Mobile.d9393b8d.svg);
        background-repeat: no-repeat;
        position: absolute;
        width: 180px;
        height: 193px;
        top: 750px;
        z-index: 1;
        align-self: flex-end;
    }
}


